.App {
    margin: 1% auto;
    background-color: #F9F8F8;
}

.header {
    width: 90%;
    margin: 1% auto;
    padding: 1%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #D00000;
    border-radius: 5px;
}

span {
    text-decoration: underline;
}

.body {
    display: flex;
    flex-direction: column;
    align-items: center;
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

input .chatbox {
    width: 90%;
    margin: 1% auto;
    height: 80%;
}